import React from 'react';

const TicketsCatalog = (props) => 
      <ul className="tickets">
        {props.tickets.map((ticket, index) => (
           <li className="thumbnail" key={index}>
              <div className="tickets__btn-buy-wrapper">
              <p>TURKISH<br/> AIRLINES</p>
              <img alt="Logo" src="img/logo.png"/>
              <button className="btn btn-success">
                 Купить<br/>за {ticket.price} $
              </button>
            </div>
            <div className="tickets__info-wrapper">
               <div>
                <p className="time">{ticket.departure_time}</p>
                <p className="info-transplan">{ticket.stops} ПЕРЕСАДКА</p>
                <p className="glyphicon glyphicon-plane">
                </p>
                <p className="time">{ticket.arrival_time}</p>
              </div>
               <div>
                 <div>
                   <p><strong>{ticket.origin}, {ticket.origin_name}</strong></p>
                   <p className="data">{ticket.departure_date}</p>
                 </div>
                 <div>
                   <p><strong>{ticket.destination_name}, {ticket.destination}</strong></p>
                  <p className="data">{ticket.arrival_date}</p> 
                 </div>                       
              </div>
            </div>
           </li>
        ))} 
      </ul>
    ;

  export default TicketsCatalog;