import React from 'react';
import {getAll} from './api/tickets';
import Filter from './Filter';
import Exchanger from './Exchanger';
import TicketsCatalog from './TicketsCatalog';
import './App.css';


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tickets: getAll(),
      ticketsView: [],
      ticketsShow: "All",
      
    } 
  }

  render () {
    let ticketsView = [];

    if (this.state.ticketsShow === "All") {
      ticketsView = this.state.tickets;
    } else if (this.state.ticketsShow === "Null") {
      ticketsView = this.state.tickets.filter(this.state.tickets.stops === 0);
    } else if (this.state.ticketsShow === "One") {
      ticketsView = this.state.tickets.filter(this.state.tickets.stops === 1);
    } else if (this.state.ticketsShow === "Two") {
      ticketsView = this.state.tickets.filter(this.state.tickets.stops === 2);
    } else if (this.state.ticketsShow === "Tree") {
      ticketsView = this.state.tickets.filter(this.state.tickets.stops === 3);
    }


    return (
      <div className="App">
        <div className="container-fluid">
          <div><img alt="Plane" src="img/plane.png"/></div>
          <div className="row">
            <div className="col-md-2">
              <section>
                <Exchanger/>
                <Filter/>
              </section>
              <section>
                <p>КОРЗИНА</p>
                <ul>
                  <li>Билет 1</li>
                  <li>Билет 2</li>
                </ul>
              </section>
            </div>
            <div className="col-md-10">
              <TicketsCatalog 
                tickets={this.state.tickets}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}





export default App;
